.header-services {
  display: none;
}
.navbar_services {
  display: block;
}
html {
  overflow-x: hidden;
}
.endo_text {
  margin-top: 3rem;
  h1 {
    font-size: 47px;
    font-family: "Si-ui-bold";
  }
  h3 {
    font-size: 36px;
    font-family: "Si-ui-semibold";
  }
}
.endo_image {
  height: 20rem;
  position: relative;
  display: flex;
  //justify-content: center;
  align-items: center;
  left: 0%;
  margin-top: 2rem;
  margin-bottom: 3rem;
  .services_image {
    // box-shadow: 17px 15px 19px 1px rgba(0, 0, 0, 0.2);
    img {
      height: 16.5rem;
    }
  }
  &:after {
    content: "\A";
    position: absolute;
    background: transparent linear-gradient(0deg, #175c64, #46c1bb) 0% 0%
      no-repeat padding-box;
    top: 0;
    bottom: 0;
    left: 40%;
    width: 99%;
    z-index: -1;
  }
}
.endo_longtext {
  h1 {
    font-family: "Si-ui-bold";
  }
  p {
    font-family: "Si-ui-light";
    letter-spacing: 0.1rem;
    font-size: 1.7rem;
  }
}
.ourexpert-schedulevisit {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
}
