// }
@media (min-width: 768px) {
  html {
    overflow-x: hidden;
  }
  .header-services {
    display: block;
  }
  // .navbar_services {
  //   display: none;
  // }
  .endodontics {
    margin-top: 3rem;
  }
  .header-services {
    display: none;
  }
  .endo_text {
    margin-top: 0rem;
    z-index: -10;
    padding: 1rem;
    padding-top: 7rem;
  }
  .endo_image {
    height: 24rem;
    left: -12%;
    margin-top: 0rem;
    margin-bottom: 0rem;
    .services_image {
      img {
        height: 20.5rem;
      }
    }
    &:after {
      left: 30%;
      width: 99%;
    }
  }
  .endo_longtext {
    margin-top: 0rem;
    padding: 1rem;
  }
}
@media (min-width: 992px) {
  html {
    overflow-x: hidden;
  }
  .header-services {
    display: block;
  }
  .navbar_services {
    display: none;
  }
  .endo_text {
    padding-top: 0rem;
    padding-left: 8rem;
    h1 {
      font-size: 70px;
      font-family: "Si-ui-bold";
    }
    h3 {
      font-size: 40px;
      font-family: "Si-ui-semibold";
    }
  }
  .endo_image {
    height: 29rem;
    left: -5%;
    padding-top: 30%;
    margin-top: -14rem;
    margin-bottom: 0rem;
    .services_image {
      img {
        height: auto;
      }
    }
    &:after {
      left: 50%;
      width: 99%;
    }
  }
  .endo_longtext {
    margin-top: -2%;
    padding-right: 51%;
    padding-left: 8rem;
  }
}

@media (min-width: 1200px) {
  .endo_text {
    padding-top: 0rem;
    padding-left: 8rem;
    padding-bottom: 2rem;
  }
  .endo_longtext {
    margin-top: -2%;
    padding-right: 56%;
    padding-left: 8rem;
  }
  #emi-container {
    margin-top: 20rem;
  }
}
@media (min-width: 1600px) {
  html {
    overflow-x: hidden;
  }
  .endo_longtext {
    margin-top: -3%;
    h1 {
      font-size: 2rem;
    }
  }
  .endo_text {
    h1 {
      font-size: 3.5rem;
    }
  }
}
