.expert-detail-container {
  color: #313133;
  overflow: hidden;
  .expert-detail {
    margin-bottom: 5rem;
    .expert-detail-image {
      height: 18rem;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      .expert-image-container {
        box-shadow: 17px 15px 19px 1px rgba(0, 0, 0, 0.2);
        img {
          height: 12.5rem;
        }
      }
      &:after {
        content: "\A";
        position: absolute;
        background: transparent linear-gradient(0deg, #175c64, #46c1bb) 0% 0%
          no-repeat padding-box;
        top: 0;
        bottom: 0;
        left: 50%;
        width: 50%;
        z-index: -1;
      }
    }
    .expert-detail-text {
      margin-top: 3rem;
      .expert-about {
        h1 {
          font-size: 30px;
          font-family: "Si-ui-bold";
        }
        p {
          font-size: 15px;
          font-family: "Si-ui-light";
        }
      }
      .expert-experience {
        color: #368487;
        font-family: "Si-ui-bold";
        margin-top: 1.5rem;
        h1 {
          font-size: 48px;
        }
        p {
          font-size: 15px;
        }
      }
    }
    .expert-description {
      margin-top: 3rem;
      p {
        font-size: 15px;
        word-spacing: 2px;
        font-family: "Si-ui-light";
      }
    }
  }

  .expert-detail.inverted {
    .expert-detail-image {
      &::after {
        left: 0%;
      }
    }
  }
}

@media (min-width: 768px) {
  .expert-detail-container {
    .expert-detail {
      .expert-detail-image {
        height: 22rem;
        .expert-image-container {
          img {
            height: 16.5rem;
            // padding-right: 4rem;
          }
        }
      }
      .expert-detail-text {
        .expert-about {
          h1 {
            font-size: 40px;
          }
          p {
            font-size: 17px;
          }
        }
        .expert-experience {
          h1 {
            font-size: 58px;
          }
          p {
            font-size: 17px;
          }
        }
      }
      .expert-description {
        p {
          font-size: 17px;
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .expert-detail-container {
    .expert-detail {
      .expert-detail-image {
        height: 30rem;
        .expert-image-container {
          img {
            height: 23rem;
          }
        }
      }
      .expert-detail-text {
        margin-top: 6rem;
      }
      .expert-description {
        margin-top: 0;
      }
    }
  }
}

@media (min-width: 1200px) {
  .expert-detail-container {
    .expert-detail {
      .expert-detail-image {
        height: 40rem;
        .expert-image-container {
          img {
            height: 28rem;
          }
        }
      }
      .expert-detail-text {
        margin-top: 11rem;
        padding: 0 5rem;
        .expert-about {
          h1 {
            font-size: 50px;
          }
          p {
            padding-top: 2.5rem;
          }
        }
        .expert-experience {
          margin-top: 2rem;
          h1 {
            font-size: 50px;
          }
        }
      }
      .expert-description {
        margin-top: -3rem;
        padding: 0 5rem;
      }
    }
  }
}
