@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.patch_test {
  width: 15rem;
  height: 25rem;
  background: transparent linear-gradient(180deg, #71d1ca 0%, #175c64 100%) 0%
    0% no-repeat padding-box;
  //margin-top: 32rem;
}
.App_test {
  position: relative;
  z-index: 2;
  width: 60em;
  margin: 0 auto;
  // height: auto;
  //background-color: blue;
  margin-left: -27rem;
  margin-top: -23rem;
  margin-bottom: 9rem;
}

.slide img {
  width: 20rem;
  // margin: 0 auto;
}

.slide {
  transform: scale(0.7);
  transition: transform 300ms;
  opacity: 1;
  //background-color: blue;
  position: relative;
  right: 0%;
  margin-left: 11rem;
  margin-top: 17%;
}

.activeSlide {
  // position: relative;
  transform: scale(0.9);
  opacity: 1;
  //background-color: pink;
  // padding-left: 10rem;
  margin-left: 1rem;
}

.arrow_test {
  display: none;
}
#text_test {
  margin-left: 0.5rem;

  h1 {
    font-family: "Si-ui-bold";
    font-size: 2rem;
  }
  p {
    font-family: "Si-ui-light";
    padding-right: 2rem;
    font-size: 1.7rem;
    letter-spacing: 0.2rem;
  }
}
.three-dots_test {
  display: none;
}
@media (min-width: 768px) {
  .patch_test {
    width: 20rem;
    height: 35rem;
    //margin-top: 32rem;
  }
  .App_test {
    width: 100em;
    //background-color: blue;
    margin-top: -35rem;
    margin-bottom: 12rem;
  }
  .slide img {
    width: 30rem;
  }
  .slide {
    //background-color: blue;
    right: 0%;
    margin-left: 4rem;
    margin-top: 17%;
  }
  .activeSlide {
    margin-left: -15rem;
  }
  #text_test {
    p {
      margin-right: -23rem;
    }
  }
  .three-dots_test {
    display: none;
  }
}
@media (min-width: 1200px) {
  .patch_test {
    width: 30rem;
    height: 40rem;
  }
  .App_test {
    position: absolute;
    z-index: 2;
    width: 70em;
    margin: -38rem auto;
    height: auto;
    //background-color: blue;
    margin-left: -20rem;
    margin-bottom: 0;
  }

  .slide img {
    width: 40rem;
    // margin: 0 auto;
  }

  .slide {
    transform: scale(0.9);
    transition: transform 300ms;
    opacity: 1;
    padding-top: 7%;
    margin-left: 13%;
  }

  .activeSlide {
    transform: scale(1.1);
    opacity: 1;
    margin-left: 16%;
  }

  .arrow_test {
    display: block;
    background-color: #fff;
    position: absolute;
    cursor: pointer;
    z-index: 10;
  }

  .arrow_test svg {
    transition: color 300ms;
  }

  .arrow_test svg:hover {
    color: #68edff;
  }

  .next_button_test {
    right: -41%;
    top: 160%;
  }

  .prev_button_test {
    left: 133%;
    top: 160%;
  }
  #text_test {
    // padding-left: 25rem;
    // padding-top: 43rem;
    position: absolute;
    left: 67%;
    margin-top: 5%;
    h1 {
      font-family: "Si-ui-bold";
      font-size: 3rem;
      margin-top: 6rem;
    }
    p {
      font-family: "Si-ui-light";
      padding-right: 38rem;
      font-size: 1.5rem;
    }
  }
  .previous1_test {
    width: 40px;
    height: 35px;
    padding-left: 0%;
    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    //    margin-top: 20%;
    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    border: 0px;
    opacity: 1;
    display: inline-flex;
    text-align: center;
    padding-top: 10px;
    padding-left: 12px;
    padding-bottom: 0.51%;
    outline: hidden;
  }
  .round1_test {
    border-radius: 50%;
    width: 36px;
    height: 35px;
    // margin-left: 20%;
    margin-top: -1.3%;
    margin-left: 10px;
    // margin-top: -200px;
  }
  .previous2_test {
    width: 40px;
    height: 35px;

    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    opacity: 1;
    display: inline-flex;
    align-items: center;
    border: 0px;
    padding-left: 13px;
    padding-bottom: 7px;
    outline: 0px;
  }
  .round2_test {
    border-radius: 50%;
    margin-left: 1%;
    margin-top: -1.2%;
    width: 36px;
    height: 35px;
  }
  .three-dots_test {
    width: 100%;
    height: 5%;
    display: inline-flex;
    padding-top: 5px !important;
    padding-left: 20% !important;
    // padding-bottom: 20% !important;
    position: absolute;
    margin-top: 7.5rem !important;
    margin-left: 28rem !important;
    span.first {
      width: 150px;
      height: 3px;
      border-radius: 50%;
      background: #e4e4e4 0% 0% no-repeat padding-box;
      display: inline-block;
      margin: 3px;
      border-radius: 20%;
      margin-left: 0%;
    }

    span.second {
      width: 150px;
      height: 3px;
      border-radius: 50%;
      background: #3aa9a1 0% 0% no-repeat padding-box;
      display: inline-block;
      margin: 3px;
      border-radius: 20%;
      margin-left: 0%;
    }
  }
}
@media (min-width: 1600px) {
  html {
    overflow-x: hidden;
  }
  .App_test {
    position: absolute;
    z-index: 2;
    width: 100em;
    margin: -44rem auto;
    height: auto;
    //background-color: blue;
    margin-left: -40rem;
    margin-bottom: 0;
  }
  #text_test {
    // padding-left: 25rem;
    // padding-top: 43rem;
    width: 40%;
    position: absolute;
    left: 60%;
    margin-top: 1%;
    h1 {
      font-family: "Si-ui-bold";
      font-size: 3rem;
      margin-top: 6rem;
    }
    p {
      font-family: "Si-ui-light";
      padding-right: 0rem;
      font-size: 1.5rem;
      margin-right: 20rem;
      overflow-x: hidden;
    }
  }
  .next_button_test {
    right: -34%;
    top: 138%;
  }

  .prev_button_test {
    left: 128.5%;
    top: 138%;
  }
  .three-dots_test {
    width: 40%;
    margin-left: 40rem !important;
  }
}
