@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.carasouel_services {
  //overflow: hidden;
  //width: 100%;
  // margin: 0rem;
  // margin-top: 5rem;
}

.slide_services img {
  width: 15rem;
  margin: 0 auto;
}

.slide_services {
  transform: scale(0.7);
  transition: transform 300ms;
  opacity: 1;
}

.activeSlide_services {
  transform: scale(1.1);
  opacity: 1;
}

.arrows {
  display: none;
}

@media (min-width: 1200px) {
  .carasouel_services {
    //overflow: hidden;
    //width: 100%;
    // margin: 0rem;
    margin-top: 4rem;
    margin-bottom: 4rem;
  }
  .slide_services img {
    width: 30rem;
    margin: 0 auto;
    border: none;
  }

  .slide_services {
    transform: scale(0.7);
    transition: transform 300ms;
    opacity: 1;
    border: none;
  }

  .activeSlide_services {
    transform: scale(1.1);
    opacity: 1;
    border: none;
  }

  .arrows {
    display: block;
    background-color: #fff;
    position: absolute;
    cursor: pointer;
    z-index: 10;
  }

  .arrow_services svg {
    transition: color 300ms;
  }

  .arrow_services svg:hover {
    color: #68edff;
  }

  .next_button_services {
    right: 1%;
    top: 57%;
  }

  .prev_button_services {
    left: 0%;
    top: 115%;
  }
  .previous1 {
    width: 40px;
    height: 35px;
    padding-left: 0%;
    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    //    margin-top: 20%;
    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    border: 0px;
    opacity: 1;
    display: inline-flex;
    text-align: center;
    padding-top: 10px;
    padding-left: 12px;
    padding-bottom: 0.51%;
    outline: hidden;
  }
  .round1 {
    border-radius: 50%;
    width: 36px;
    height: 35px;
    // margin-left: 20%;
    margin-top: -1.3%;
    margin-left: 10px;
    // margin-top: -200px;
  }
  .previous2 {
    width: 40px;
    height: 35px;

    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    opacity: 1;
    display: inline-flex;
    align-items: center;
    border: 0px;
    padding-left: 13px;
    padding-bottom: 7px;
    outline: 0px;
  }
  .round2 {
    border-radius: 50%;
    margin-left: 1%;
    margin-top: -1.2%;
    width: 36px;
    height: 35px;
  }
}
@media (min-width: 1600px) {
  .prev_button_services {
    left: 2%;
    top: 115%;
  }
  .next_button_services {
    right: 2%;
    top: 65%;
  }
  .slide_services {
    transform: scale(0.9);
    transition: transform 300ms;
    opacity: 1;
    border: none;
  }

  .activeSlide_services {
    transform: scale(1.1);
    opacity: 1;
    border: none;
  }
}
