section {
  background-image: linear-gradient(#175c64, #46c1bb, white);
  // perspective: 1rem;
  width: 100%;
  height: 120vh;
}
.sky {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  overflow: hidden;
  position: absolute;
  .sky_top {
    height: 60vh;
    width: 100%;
    background-image: url(../../assets/3.png);
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    top: 3rem;
    animation: bg-animation_top 10s linear infinite;
  }
  .sky_bottom {
    height: 60vh;
    width: 100%;
    background-image: url(../../assets/4.png);
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    left: 50%;
    bottom: 11rem;
    z-index: 1;
    animation: bg-animation 10s linear infinite;
  }
}

@keyframes bg-animation {
  100% {
    transform: translateX(-20rem);
  }
}
@keyframes bg-animation_top {
  100% {
    transform: translateX(20rem);
  }
}
.mouseover_animation {
  height: 100vh;
}

.mouseover_animation_mobile {
  height: 100vh;
}

.hand_animation {
  position: absolute;
  overflow: hidden;
  top: 0;
  height: 100vh;
  width: 100%;
  z-index: 2;

  #left_hand {
    img {
      width: 75%;
      margin-left: 0rem;
      margin-top: 8rem;
      position: relative;
      right: 45%;
      top: 39%;
    }
  }
  #right_hand {
    img {
      width: 75%;
      float: right;
      margin-right: 0rem;
      margin-top: 12rem;
      position: relative;
      left: 54%;
    }
  }
}

.home-schedulevisit {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
  opacity: 1;
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) {
  .hand_animation {
    #left_hand {
      img {
        width: 60%;
        position: relative;
        right: 45%;
        top: 39%;
      }
    }
    #right_hand {
      img {
        width: 60%;
        margin-right: 0rem;
        position: relative;
        left: 54%;
      }
    }
  }
  @keyframes bg-animation {
    100% {
      transform: translateX(-30rem);
    }
  }
  @keyframes bg-animation_top {
    100% {
      transform: translateX(30rem);
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
  .hand_animation {
    #left_hand {
      img {
        width: 50%;
        margin-left: -15rem;
        margin-top: 10rem;
      }
    }
    #right_hand {
      img {
        width: 50%;
        margin-right: -15rem;
        margin-top: 10rem;
      }
    }
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
  .hand_animation {
    #left_hand {
      img {
        width: 40%;
        margin-left: -8rem;
        margin-top: 15rem;
        left: 0%;
        right: 0%;
      }
    }
    #right_hand {
      img {
        width: 40%;
        margin-right: -10rem;
        margin-top: 10rem;
        left: 0%;
        right: 0%;
      }
    }
  }
}

// X-Large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
  .hand_animation {
    #left_hand {
      img {
        width: 30%;
        margin-left: -5rem;
        margin-top: 10rem;
        position: relative;
        top: 30%;
      }
    }
    #right_hand {
      img {
        width: 30%;
        margin-right: -9rem;
        margin-top: 8rem;
      }
    }
  }
  .sky_top {
    top: 1rem;
  }
}

// XX-Large devices (larger desktops, 1400px and up)
@media (min-width: 1600px) {
  .hand_animation {
    #left_hand {
      img {
        width: 30%;
        margin-left: -5rem;
        margin-top: 10rem;
        position: relative;
        top: 47%;
      }
    }
    #right_hand {
      img {
        width: 40%;
        margin-right: -20rem;
        margin-top: 18rem;
        left: 0%;
        right: 0%;
      }
    }
  }
}
