.expert-header {
  display: none;
  margin-bottom: 0%;
}

.expert-navbar {
  display: block;
}
.expert-carousel {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
}

@media (min-width: 1196px) {
  .expert-header {
    display: block;
    margin-bottom: -11.5rem;
  }

  .expert-navbar {
    display: none;
  }
}
