.treatment_treatments {
  // background-color: purple;
  width: 100%;
  margin-top: 5rem;
  margin-bottom: 5rem;
  padding-left: 2rem;
  padding-right: 1rem;
  h1 {
    font-size: 2rem;
    letter-spacing: 1rem;
    opacity: 0.1;
    color: #000000;
    text-align: center;
    justify-content: center;
    display: flex;
  }
}
.treatment_schedule {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
}
@media (min-width: 768px) {
  .treatment_treatments {
    h1 {
      font-size: 4rem;
      letter-spacing: 2rem;
    }
  }
}
@media (min-width: 992px) {
  .treatment_treatments {
    h1 {
      font-size: 4rem;
      letter-spacing: 2rem;
    }
  }
}
@media (min-width: 1200px) {
  .treatment_treatments {
    h1 {
      font-size: 5rem;
      letter-spacing: 3rem;
    }
  }
}
