@media (min-width: 992px) {
  .togglebar {
    h1 {
      margin: 0;
      color: #ddd;
      padding: 10px;
      border-bottom: 1px solid #666;
    }
    button:focus {
      outline: none;
    }
    aside {
      background: #1a1d23;
      width: 250px;
      height: auto;
      position: fixed;
      transition: 0.3s;
      left: -250px;
      top: 0;
      transition-timing-function: cubic-bezier(0.9, 0, 1, 1);
      margin-top: 4rem;
      z-index: 99;
    }
    aside.close {
      left: 0;
      transition: 0.3s;
      transition-timing-function: cubic-bezier(0.9, 0, 1, 1);
    }
    nav a {
      display: block;
      color: #ddd;
      text-decoration: none;
      padding: 10px;
      padding-top: 4rem;
      font-size: 2rem;
      font-family: "Si-ui-bold";
    }
    nav a:hover {
      background: #313640;
    }
    .toggle {
      border: none;
      background: none;
      position: fixed;
      right: 90%;
      top: 37%;
      font-size: 2rem;
      transform: rotate(90deg);
      display: inline-block;
      cursor: pointer;
      z-index: -1;
    }
  }
}
