.about-navbar {
  display: block;
}

.about-header {
  display: none;
}

.about-container {
  .about-top-container {
    position: relative;
    h1 {
      color: rgb(58, 169, 169);
      font-family: "Si-ui-bold";
      font-size: xx-large;
    }
    &::after {
      content: "\A";
      position: absolute;
      background: #368487;
      top: 0;
      left: 50%;
      right: 0;
      width: 55%;
      height: 100%;
      z-index: -1;
    }
    .about-image-container {
      display: flex;
      justify-content: center;
      padding: 5% 0% 10% 0%;
      img {
        height: 14rem;
        width: 85%;
      }
    }
  }

  .about-toptext-container {
    padding-top: 10%;
    h1 {
      font-family: "Si-ui-bold";
      font-size: xx-large;
    }
    p {
      font-family: "Si-ui-light";
      font-size: large;
    }
  }

  .about-middle-container {
    margin-top: 10%;
    position: relative;
    &::after {
      content: "\A";
      position: absolute;
      background: #368487;
      top: 0;
      right: 40%;
      left: -5%;
      width: 55%;
      height: 100%;
      z-index: -1;
    }
    .about-image-container {
      padding: 10% 0% 10% 0%;
      display: flex;
      justify-content: center;
      img {
        height: 14rem;
        width: 85%;
      }
    }
  }

  .about-middletext-container {
    padding-top: 10%;
    h1 {
      font-family: "Si-ui-bold";
      font-size: xx-large;
    }
    p {
      font-family: "Si-ui-light";
      font-size: large;
    }
  }

  .about-buttom-container {
    padding-top: 10%;
    h1 {
      text-align: center;
      font-family: "Si-ui-bold";
      font-size: xx-large;
      padding-bottom: 5%;
    }
  }
}

.about-schedulevisit {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
}

@media (min-width: 768px) {
  .about-container {
    .about-top-container {
      h1 {
        font-size: 2rem !important;
      }
      .about-image-container {
        img {
          height: 27rem !important;
        }
      }
    }

    .about-toptext-container {
      h1 {
        font-size: 2rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }

    .about-middle-container {
      .about-image-container {
        img {
          height: 27rem !important;
        }
      }
    }

    .about-middletext-container {
      h1 {
        font-size: 2rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }

    .about-buttom-container {
      h1 {
        font-size: 2rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }
  }
}

