  .about-navbar {
    display: none;
  }
  .about-header {
    display: block;
  }
  .about-container {
    .about-top-container {
      h1 {
        font-size: 2.5rem !important;
      }
      .about-image-container {
        img {
          height: 33rem !important;
        }
      }
    }

    .about-toptext-container {
      h1 {
        font-size: 2.5rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }

    .about-middle-container {
      .about-image-container {
        img {
          height: 33rem !important;
        }
      }
    }

    .about-middletext-container {
      h1 {
        font-size: 2.5rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }

    .about-buttom-container {
      h1 {
        font-size: 2.5rem !important;
      }
      p {
        font-size: 1rem !important;
      }
    }
  }
}

@media (min-width: 1200px) {
  .about-container {
    .about-top-container {
      h1 {
        font-size: x-large;
        padding-left: 5%;
      }
      &::after {
        top: -30%;
        height: 75%;
      }
      .about-image-container {
        padding-bottom: 0% !important;
        img {
          height: 33rem !important;
          width: 65% !important;
        }
      }
    }
    .about-toptext-container {
      padding-top: 5% !important;
      padding-left: 5%;
      h1 {
        font-size: xx-large !important;
      }
      p {
        font-size: medium !important;
      }
    }
    .about-middle-container {
      margin-top: 5%;
      .about-image-container {
        padding: 5% 0% 5% 0%;
        img {
          height: 30rem !important;
          width: 60% !important;
        }
      }
    }

    .about-middletext-container {
      padding-top: 5% !important;
      padding-left: 5%;
      h1 {
        font-size: xx-large !important;
      }
      p {
        font-size: medium !important;
      }
    }

    .about-buttom-container {
      padding-top: 5%;
      h1 {
        font-size: xx-large !important;
      }
    }
  }
}

@media (min-width: 1750px) {
  .about-container {
    .about-top-container {
      h1 {
        font-size: x-large;
      }
      &::after {
        height: 60%;
      }
      .about-image-container {
        padding-bottom: 0% !important;
        img {
          height: 52rem !important;
          width: 70% !important;
        }
      }
    }
    .about-toptext-container {
      padding-top: 5% !important;
