@import "../../../node_modules/slick-carousel/slick/slick.css";
@import "../../../node_modules/slick-carousel/slick/slick-theme.css";

.onevisit-container {
  overflow: hidden;
  .onevisit-top-container {
    .onevisit-top-text {
      padding-bottom: 5%;
      h1 {
        font-family: "Si-ui-bold";
      }
      p {
        font-family: "Si-ui-light";
      }
    }
    .onevisit-top-patch {
      position: relative;

      &::after {
        content: "\A";
        position: absolute;
        background: #368487;
        top: 0;
        left: 35%;
        right: 0;
        width: 70%;
        height: 100%;
        z-index: -1;
      }
      .onevisit-topimage-container {
        display: flex;
        justify-content: center;
        .onevisit-top-image {
          width: 95%;
          height: 20rem;
          padding-top: 10%;
        }
      }
    }
  }

  .onevisit-middle-container {
    .onevisit-middle-patch {
      position: relative;
      &::after {
        content: "\A";
        position: absolute;
        background: #368487;
        top: 0;
        right: 35%;
        left: 0;
        width: 70%;
        height: 100%;
        z-index: -1;
      }
      .onevisit-middleimage-container {
        display: flex;
        justify-content: center;
        padding: 10% 0% 10% 0%;
        .onevisit-middleimage {
          width: 95%;
          height: 18rem;
        }
      }
    }

    .onevisit-middle-text {
      margin-top: 2rem;
      padding-top: 5%;
      h1 {
        font-family: "Si-ui-bold";
      }
      p {
        font-family: "Si-ui-light";
      }
      .onevisit-btn {
        border-radius: 5rem;
        background: #69c6c0 0% 0% no-repeat padding-box;
        border: none;
        width: 6rem;
      }
    }
  }

  .onevisit-dental-container {
    margin-top: 20%;
    .onevisit-dental-text {
      h1 {
        font-family: "Si-ui-bold";
      }
      h3 {
        font-family: "Si-ui-bold";
      }
      p {
        font-family: "Si-ui-light";
      }
      .onevisit-btn {
        border-radius: 5rem;
        background: #69c6c0 0% 0% no-repeat padding-box;
        border: none;
        width: 6rem;
      }
    }

    .onevisit-dentalimage-container {
      padding-top: 10%;
      display: flex;
      justify-content: center;
      .onevisit-dental-image {
        width: 100%;
        height: 20rem;
        animation: shake2-animation 3s ease-Out infinite;
      }

      @keyframes shake2-animation {
        0% {
          transform: translate(0, 0);
        }

        25% {
          transform: translate(0px, 20px);
        }

        50% {
          transform: translate(0, 0);
        }

        75% {
          transform: translate(0px, 20px);
        }

        100% {
          transform: translate(0, 0);
        }
      }
    }
  }

  .counter_desktop {
    background: transparent
      linear-gradient(180deg, #ffffff 0%, #46c1bb 0%, #175c64 100%) 0% 0%
      no-repeat padding-box;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3% 0% 3% 0%;
    color: white;
    font-style: "Si-ui-bold";
  }

  .treatment-container {
    position: relative;
    letter-spacing: 7.79px;
    opacity: 0.1;
    color: #000000;
    .treatment-text {
      font-size: 3rem;
      font-family: "Si-ui-bold";
    }
  }
  .visit_carousel {
    margin-top: 10rem;
    //margin-bottom: 10rem;
  }
}

@media screen and (min-width: 768px) {
  .onevisit-container {
    .onevisit-top-container {
      .onevisit-top-text {
        h1 {
          font-size: 2.5rem !important;
        }
        p {
          font-size: 1.5rem !important;
        }
      }
      .onevisit-top-patch {
        .onevisit-topimage-container {
          .onevisit-top-image {
            height: 40rem !important;
          }
        }
      }
    }

    .onevisit-middle-container {
      .onevisit-middle-patch {
        .onevisit-middleimage-container {
          .onevisit-middleimage {
            height: 30rem !important;
          }
        }
      }

      .onevisit-middle-text {
        h1 {
          font-size: 2.5rem !important;
        }
        p {
          font-size: 1.5rem !important;
        }
      }
    }

    .onevisit-dental-container {
      .onevisit-dental-text {
        h1 {
          font-size: 2.5rem !important;
        }
        h3 {
          font-size: 1.4rem !important;
        }
        p {
          font-size: 1.5rem !important;
        }
      }

      .onevisit-dentalimage-container {
        .onevisit-dental-image {
          height: 40rem;
        }
      }
    }
    .treatment-container {
      position: relative;
      letter-spacing: 39.79px;
      opacity: 0.1;
      color: #000000;
      .treatment-text {
        font-size: 6rem;
        font-family: "Si-ui-bold";
      }
    }
  }
}

