.ourexpert-header {
  display: none;
}

.ourexpert-navbar {
  display: block;
}

.ourexpert-container {
  .ourexpert-front-back {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    &::after {
      content: "\A";
      position: absolute;
      background: #368487;
      top: 0;
      left: 35%;
      right: 0;
      width: 70%;
      height: 100%;
      z-index: -1;
    }
    .ourexpert-image-container {
      margin-top: 10%;
      padding-left: 10%;
      img {
        height: 16rem;
        width: 90%;
      }
    }
  }

  .ourexpert-text-container {
    margin-top: 15%;
    .ourexpert-text-heading {
      font-size: 2rem;
      font-family: "Si-ui-bold";
      padding-right: 25%;
    }
    .ourexpert-text-description {
      font-family: "Si-ui-light";
      font-size: 1rem;
    }
  }
}

.ourexpert-schedulevisit {
  background: transparent
    linear-gradient(180deg, #ffffff 0%, #46c1bb 41%, #175c64 100%) 0% 0%
    no-repeat padding-box;
}

@media (min-width: 768px) {
  .ourexpert-container {
    .ourexpert-front-back {
      .ourexpert-image-container {
        img {
          padding-left: 10%;
          height: 31rem;
          width: 90%;
        }
      }
    }

    .ourexpert-text-container {
      .ourexpert-text-heading {
        padding-right: 40%;
        font-size: 3rem;
      }
      .ourexpert-text-description {
        font-size: 1.2rem;
      }
    }
  }
}

@media (min-width: 992px) {
  .ourexpert-container {
    .ourexpert-front-back {
      &::after {
        height: 70%;
        top: -10%;
      }
    }
    margin-top: -9rem;
    .ourexpert-header {
      display: block;
    }

    .ourexpert-navbar {
      display: none;
    }
    .ourexpert-text-container {
      padding-right: 40%;
      margin-top: 10%;
      .ourexpert-text-description {
        font-size: 1.2rem;
      }
    }
  }
}

@media (min-width: 1200px) {
  .ourexpert-container {
    .ourexpert-front-back {
      .ourexpert-image-container {
        padding-left: 15%;
        img {
          height: 35rem;
          width: 75%;
        }
      }
      &::after {
        left: 60%;
        height: 50%;
      }
    }

    .ourexpert-text-container {
      margin-top: 5%;
      padding-left: 5%;
      padding-right: 0%;
      .ourexpert-text-heading {
        font-size: 3rem;
      }
      .ourexpert-text-description {
        font-size: 1rem;
      }
    }
  }
  .ourexpert_carousel {
    margin-bottom: 9rem;
  }
}

@media (min-width: 1750px) {
  .ourexpert-container {
    .ourexpert-front-back {
      .ourexpert-image-container {
        img {
          height: 55rem;
        }
      }
    }

    .ourexpert-text-container {
      .ourexpert-text-heading {
        font-size: 3rem;
      }
      .ourexpert-text-description {
        font-size: 2rem;
      }
    }
  }
}
