.carouselShow4_onevisit {
  padding-left: 1rem;
  // width: 100em;
  //margin-top: 0rem;
  margin: 0 auto;

  .patch {
    width: 12rem;
    height: 22rem;
    background: transparent linear-gradient(180deg, #46c1bb 0%, #175c64 100%) 0%
      0% no-repeat padding-box;
    h1 {
      font-family: "Si-ui-bold";
      font-size: 1rem;
      padding-top: 12rem;
      padding-left: 2rem;
      color: white;
    }
    p {
      font-family: "Si-ui-light";
      font-size: 1rem;
      color: white;
      padding-left: 2rem;
      letter-spacing: 2px;
    }
    .btn {
      width: 6rem;
      height: 2rem;
      background: #69c6c0 0% 0% no-repeat padding-box;
      border-radius: 3.7rem;
      margin-left: 2rem;
      font-size: 13px;
      color: #f7f7f7;
      line-height: 0rem;
      border: none;
    }
  }
}
.pics {
  width: 14rem;
  height: 16rem;
  position: absolute;
  z-index: 2;
  margin-top: -26rem;
  margin-left: -2rem;
}
.slides {
  transform: scale(0.7);
  transition: transform 300ms;
  //background-color: blue;
  opacity: 1;
  // padding-left: 0rem;
  margin-top: -1rem;
  margin-left: 9px;
  // padding-right: 27rem;
}

.activeSlides {
  //background-color: pink;
  transform: scale(0.8);
  opacity: 1;
  //padding-right: 80rem;
  margin-top: 1rem;
  margin-left: 0rem;
}
.arrows {
  display: none;
}
.three-dots {
  display: none;
}
@media (min-width: 768px) {
  .carouselShow4_onevisit {
    // padding-left: 1rem;
    // margin-top: 0rem;

    .patch {
      width: 15rem;
      height: 30rem;
      h1 {
        font-size: 1.5rem;
        padding-top: 14rem;
      }
      .btn {
        width: 11rem;
        height: 3rem;
        background: #69c6c0 0% 0% no-repeat padding-box;
        border-radius: 3.7rem;
        margin-left: 2rem;
        font-size: 1.5rem;
        color: #f7f7f7;
        line-height: 2rem;
        border: none;
        margin-top: 1rem;
      }
    }
  }
  .pics {
    width: 100%;
    height: auto;
    margin-top: -33rem;
    margin-left: -3rem;
  }
  .slides {
    transform: scale(0.7);
    margin-top: -2rem;
    margin-left: 2rem;
  }
  .slick-slide {
    // margin-left: 10px;
  }

  .activeSlides {
    transform: scale(0.9);
    opacity: 1;
    //padding-ight: 80rem;
    // margin-top: 10rem;
    margin-left: 1rem;
    margin-top: 2rem;
  }
  .arrows {
    display: none;
  }
  .three-dots {
    display: none;
  }
}
@media (min-width: 1200px) {
  .carouselShow4_onevisit {
    width: 100em;

    .patch {
      width: 21rem;
      height: 30rem;
      h1 {
        font-size: 1.5rem;
        padding-top: 16rem;
      }
      .btn {
        width: 11rem;
        height: 3rem;
        margin-left: 2rem;
        font-size: 1.5rem;
        color: #f7f7f7;
        line-height: 2rem;
        margin-top: 2rem;
        border: none;
      }
    }
  }
  .pics {
    width: 100%;
    height: auto;
    margin-top: -38rem;
    margin-left: -6rem;
  }
  .slides {
    transform: scale(0.8);
    transition: transform 300ms;
    opacity: 1;
    margin-top: 3rem;
    margin-left: 5rem;
  }

  .activeSlides {
    transform: scale(1);
    opacity: 1;
    margin-left: 6rem;
    margin-top: 7rem;
  }
  .arrows {
    display: block;
    background-color: white;
    position: absolute;
    cursor: pointer;
    z-index: 10;
  }

  .arrows svg {
    transition: color 300ms;
  }

  .arrows svg:hover {
    color: #68edff;
  }

  .next_button {
    top: 107%;
    left: 9%;
  }

  .prev_button {
    left: 6%;
    top: 107%;
  }
  .previous1 {
    width: 40px;
    height: 35px;
    padding-left: 0%;
    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    //    margin-top: 20%;
    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    border: 0px;
    opacity: 1;
    display: inline-flex;
    text-align: center;
    padding-top: 10px;
    padding-left: 12px;
    padding-bottom: 0.51%;
    outline: hidden;
  }
  .round1 {
    border-radius: 50%;
    width: 36px;
    height: 35px;
    // margin-left: 20%;
    margin-top: -1.3%;
    margin-left: 10px;
    // margin-top: -200px;
  }
  .previous2 {
    width: 40px;
    height: 35px;

    background-color: #68c5bf;

    font: normal normal 34px/11px SF UI Display;

    color: white;
    text-shadow: 0px 3px 6px #40404029;
    font-family: sans-serif;
    white-space: nowrap;
    opacity: 1;
    display: inline-flex;
    align-items: center;
    border: 0px;
    padding-left: 13px;
    padding-bottom: 7px;
    outline: 0px;
  }
  .round2 {
    border-radius: 50%;
    margin-left: 1%;
    margin-top: -1.2%;
    width: 36px;
    height: 35px;
  }
  .three-dots {
    width: 100%;
    height: 5%;
    display: inline-flex;
    padding-top: 5px !important;
    padding-left: 20% !important;
    // padding-bottom: 20% !important;
    position: absolute;
    margin-top: 50px !important;
    margin-left: -60px;
    // display: none;

    span.first {
      width: 150px;
      height: 3px;
      border-radius: 50%;
      background: #e4e4e4 0% 0% no-repeat padding-box;
      display: inline-block;
      margin: 3px;
      border-radius: 20%;
      margin-left: 0%;
    }

    span.second {
      width: 150px;
      height: 3px;
      border-radius: 50%;
      background: #3aa9a1 0% 0% no-repeat padding-box;
      display: inline-block;
      margin: 3px;
      border-radius: 20%;
      margin-left: 0%;
    }
  }
}
@media (min-width: 1600px) {
  .three-dots {
    margin-left: 0%;
  }
}
